    <div>
      <h2>👉 THIÊT KẾ ĐẸP, HOÀN THIỆN TỐT</h2>
      <p>Với thiết kế vuông bo tròn 4 góc kết hợp với màn hình tràn viền ở mặt trước, W26 Smartwatch có thiết kế mặt
