      <p>W26 Smartwatch có màn hình 1.75inch IPS, độ phân giải 320 x 385 pixels, cảm ứng mượt mà. Thiết kế dạng tràn
        viền cực đẹp, chi tiết hiển thị sắc nét, màu sắc rực rỡ ngay cả trong điều kiện ngoài trời. Kích thước mặt của
        W26 cũng to hơn so với các Smartwatch trong cùng tầm giá.</p>
